import server from '../../server.js';
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			name:[],
			sex:'♀',
			gz:'+关注',
			TabCur: 0,
			CustomBar: this.CustomBar,
			page:1,
			weikong:true,
			postList:[],
			MyzcList:[],
			auid:0,
			status: 'loading',
			statusTypes: [{
				value: 'more',
				text: '加载前',
				checked: true
			}, {
				value: 'loading',
				text: '加载中',
				checked: false
			}, {
				value: 'noMore',
				text: '我是有底线的',
				checked: false
			}],
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			}
		}
	},
	onLoad:function(e){
		console.log(e.auid)
		this.auid=e.auid;
		uni.request({
			method:'GET',
			url: 'https://api.angeli.top/user.php?type=getUserInfo', //仅为示例，并非真实接口地址。
			data: {
				auid: e.auid,
				token:server.token
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
			},
			success: (res) => {
				console.log(res)
				this.name=res.data.data
				this.getPostList(this.name.Auid)
				if(res.data.data.guanzhu==true){
					this.gz="取消关注";
				}
				
			}
		});
		if(this.name.Gender=="2"){
			this.sex='♀'
		}else{
			this.sex='♂'
		}
	},
	methods: {
		getMyzc:function(){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=getMyGive', //仅为示例，并非真实接口地址。
				data: {
					page:1,
					count:20,
					auid:this.auid,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					
					if(res.data.code!=="1"){
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							duration:2000,
							mask:true
						});
						this.weikong=true
					}else{
						
						this.MyzcList=res.data.data
						this.weikong=false
					}
					
				},
				complete() {
					uni.hideLoading();
				}
			});
		},
		getChat:function(id){
			let go=parseInt(id)+parseInt(server.userinfo.Auid);
			uni.navigateTo({
				url: '../menu/chat?id='+go+'&toid='+this.auid
			});
		},
		tabSelect(e) {
			this.TabCur = e
			if(e==1){
				this.getMyzc()
			}
		},
		getGuanzhu:function(uid){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=gzORungz', //仅为示例，并非真实接口地址。
				data: {
					uid: uid,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',

				},
				success: (res) => {
					uni.showToast({
						title: res.data.msg,
						position:'bottom',
						icon:'none'
					});
					if(res.data.msg=='关注成功'){
						this.gz="取消关注";
					}else{
						this.gz="+关注";
					}
				}
			}); 
		},
		getPost:function(id){
			console.log(id)
			uni.navigateTo({
				url: '../postinfo/postinfo?id='+id
			});
		},
		getEditInfo:function(){
			uni.navigateTo({
				url: '../editinfo/editinfo'
			});
		},
		getPostList(auid){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=myPostList', //仅为示例，并非真实接口地址。
				data: {
					uid: auid,
					count:10,
					page:this.page,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					if(res.data.data.length==undefined){
						this.status ="noMore";
						this.weikong=true;
					}else{
						this.postList=res.data.data;
						this.status ="more";
						this.weikong=false;
						console.log(this.postList);
					}
				}
			}); 
		}
	},
	onReachBottom:function(){
		this.status ="loading";
		this.page++;
		console.log("拉倒低了",this.page)
		uni.request({
			method:'GET',
			url: 'https://api.angeli.top/post.php?type=myPostList', //仅为示例，并非真实接口地址。
			data: {
				uid: this.name.Auid,
				count:10,
				page:this.page,
				token:server.token
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
			},
			success: (res) => {
				if(res.data.data.length==undefined){
					this.page--;
					this.status ="noMore";
				}else{
					this.postList=this.postList.concat(res.data.data);
					this.status ="more";
					console.log(this.postList);
				}
				
			}
		});
	}
}
